
import {DeviceFrameset} from 'react-device-frameset'
import 'react-device-frameset/styles/marvel-devices.min.css'

import Box from '@mui/material/Box'
import Paper from '@mui/material/Paper'

import createTheme from '@mui/material/styles/createTheme';
import { ThemeProvider } from '@emotion/react';

import Header from './Header'
import ResourceStack from './ResourceStack'
import DialogSettings from './DialogSettings'
import Preview from './Preview'
import Editor from './Editor'
import DialogDownload from './DialogDownload'

import {State, Resource, Setting} from './State'

import {useState, useEffect, useRef} from 'react'

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});
const lightTheme = createTheme({
    palette: {
        mode: 'light',
    },
});

export const Demonstration = (props: any) => {

    const [state, setState] = useState<State>({
        resources: [
            {
                id: 0,
                name: "my_package",
                resource: "package",
                settings: [
                    {
                        title: "requirements",
                        type: "code",
                        value: "pandas\nnumpy",
                        options: [],
                    },
                ],
            },
            {
                id: 1,
                name: "my_folder",
                resource: "folder",
                settings: [],
            },
            {
                id: 2,
                name: "my_table",
                resource: "table",
                settings: [
                    {
                        title: "key",
                        type: "text",
                        value: "id",
                        options: [],
                    }
                ],
            },
            {
                id: 3,
                name: "my_library",
                resource: "library",
                settings: [
                    {
                        title: "import",
                        type: "text",
                        value: "csv",
                        options: [],
                    }
                ],
            },
            {
                id: 4,
                name: "my_script",
                resource: "script",
                settings: [
                    {
                        title: "content",
                        type: "code",
                        value: "def function1():\n\tpass\ndef function2():\n\tpass",
                        options: [],
                    }
                ],
            },
            {
                id: 5,
                name: "my_routine",
                resource: "routine",
                settings: [
                    {
                        title: "cron",
                        type: "text",
                        value: "* * * * *",
                        options: [],
                    },
                    {
                        title: "content",
                        type: "code",
                        value: "def handler(event):\n\tmy_script.function1()",
                        options: [],
                    }
                ],
            },
            {
                id: 6,
                name: "my_endpoint",
                resource: "endpoint",
                settings: [
                    {
                        title: "path",
                        type: "text",
                        value: "/items",
                        options: [],
                    },
                    {
                        title: "method",
                        type: "select",
                        value: "PUT",
                        options: ["GET", "PUT", "POST", "DELETE"],
                    },
                    {
                        title: "content",
                        type: "code",
                        value: "def handler(event):\n\tmy_script.function2()",
                        options: [],
                    },
                ],
            },
        ],
        theme: "light",
        font: "small",
        size: 12,
        dialog: "",
        editing: false,
        resource: -1,
        setting: "",
        output: "",
        menuId: "",
        menuType: "",
        menuX: 0,
        menuY: 0,
    })

    const [code, setCode] = useState("")
    useEffect(() => {
        setState({
            ...state,
            resources: state.resources.map((resource) => {
                if(resource.id === state.resource) {
                    return {
                        ...resource,
                        settings: resource.settings.map((setting) => {
                            if(setting.title === state.setting) {
                                return {
                                    ...setting,
                                    value: code,
                                }
                            }
                            return setting
                        })
                    }
                }
                return resource
            })
        })
    }, [code])

    useEffect(() => {
        for(let i = 0; i < state.resources.length; i++) {
            if (state.resources[i].id === state.resource) {
                for(let j = 0; j < state.resources[i].settings.length; j++) {
                    if (state.resources[i].settings[j].title === state.setting) {
                        setCode(state.resources[i].settings[j].value)
                        break
                    }
                }
                break
            }
        }
    }, [state.editing])

    const [width, setWidth] = useState(window.innerWidth / 2)
    useEffect(() => {
        const handleResize = () => {
            setWidth(window.innerWidth / 2);
        };
        window.addEventListener('resize', handleResize)
        return () => {
            window.removeEventListener('resize', handleResize)
        }
    }, [])


    return (

        <DeviceFrameset device='MacBook Pro' width={width} style={{zIndex: 1, overflow: "hidden"}}>
            {/* <AddResourceMenu state={state} setState={setState} /> */}
            <ThemeProvider theme={state.theme === "dark" ? darkTheme : lightTheme}>
            <Paper variant="outlined" sx={{position: "absolute", top: 0, bottom: 0, left: 0, right: 0, borderRadius: 0, border: 0, fontSize: state.size}}>
                {/* header */}
                <Box position="absolute" top="0em" height="5.5em" left="0em" right="0em">
                    <Box position="absolute" top="1em" bottom="0.5em" left="1em" right="1em">
                        <Paper variant="outlined" sx={{position: "absolute", top: "0em", bottom: "0em", left: "0em", right: "0em"}}>
                            <Header state={state} setState={setState} />
                        </Paper>
                    </Box>
                </Box>
                {/* resources */}
                <Box position="absolute" top="5.5em" bottom="0em" left="0em" width="20em" sx={{userSelect: "none"}}>
                    <Box position="absolute" top="0.5em" bottom="1em" left="1em" right="0.5em" sx={{userSelect: "none"}}>
                        <ResourceStack state={state} setState={setState} />
                    </Box>
                </Box>
                {/* preview */}
                <Box position="absolute" top="5.5em" bottom="0em" left="20em" right="0em">
                    <Box position="absolute" top="0.5em" bottom="1em" left="0.5em" right="1em">
                        <Preview state={state} setState={setState} />
                        <Editor state={state} setState={setState} code={code} setCode={setCode} />
                    </Box>
                </Box>
                <DialogSettings state={state} setState={setState} />
                <DialogDownload state={state} setState={setState} />
            </Paper>
            </ThemeProvider>
        </DeviceFrameset>

    )
}
export default Demonstration