import {Reorder} from 'framer-motion'

import Box from '@mui/material/Box'
import Drawer from '@mui/material/Drawer'
import Paper from '@mui/material/Paper'
import Button from '@mui/material/Button'
import Add from '@mui/icons-material/Add'

import {State} from "./State"
import ResourceCard from './ResourceCard'

import { useState, useEffect } from 'react'


export const ResourceStack = (props: {
    state: State
    setState: (state: State) => void
}) => {

    const [order, setOrder] = useState(props.state.resources)
    useEffect(() => {
        props.setState({
            ...props.state,
            resources: order,
        })
    }, [order])

    return (
        <Box position="absolute" top="0em" bottom="0em" left="0em" right="0em" sx={{userSelect: "none"}}>
            <Drawer hideBackdrop variant="permanent" transitionDuration={0} 
                PaperProps={{ 
                    sx: { 
                        position: "absolute", top: 0, bottom: 0, left: 0, paddingRight: "2em", border: "0px", zIndex: 0 } }}>
                <Reorder.Group values={order} onReorder={(newOrder: any) => {
                    setOrder(newOrder)
                }} style={{ padding: 0, margin: 0 }}>
                    {
                        order.map((resource: any) => {
                            return (
                                <ResourceCard key={resource.id} state={props.state} setState={props.setState} order={order} setOrder={setOrder} resource={resource} expanded={false} />
                            )
                        })
                    }
                </Reorder.Group>
                <Box height="4em" marginLeft="0em" marginRight="0em">
                    <Paper variant="outlined" sx={{height:"4em", width: "18.5em", display:"flex", alignItems:"center", justifyContent:"center"}}>
                        <Button color="inherit" sx={{height:"100%", width:"100%", display:"flex", alignItems:"center", justifyContent:"center"}}
                            onClick={() => {
                                props.setState({
                                    ...props.state,
                                    dialog: "download",
                                })
                            }}
                        >
                            <Add sx={{fontSize: props.state.size * 2}} />
                        </Button>
                    </Paper>
                </Box>
            </Drawer>
        </Box>
    )
}
export default ResourceStack