
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';

import Notes from '@mui/icons-material/Notes';
import Check from '@mui/icons-material/Check';
import Code from '@mui/icons-material/Code';

import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'
import ListItemText from '@mui/material/ListItemText'
import { ClickAwayListener } from '@mui/material';


import {State, Resource, Setting} from './State'

import {useState, useEffect} from 'react'

export const ResourceSetting = (props: {
    state: State,
    setState: (state: State) => void,
    order: Resource[],
    setOrder: (order: Resource[]) => void,
    resource: Resource,
    setting: Setting,
}) => {

    const [value, setValue] = useState(props.setting.value)

    const [open, setOpen] = useState(false)

    useEffect(() => {
        let newOrder = props.order

        props.setState({
            ...props.state,
            resources: props.state.resources.map((resource) => {
                if(resource.id === props.resource.id) {
                    return {
                        ...resource,
                        settings: resource.settings.map((setting) => {
                            if(setting.title === props.setting.title) {
                                return {
                                    ...setting,
                                    value: value,
                                }
                            }
                            return setting
                        })
                    }
                }
                return resource
            })
        })
    }, [value])


    useEffect(() => {
        setValue(props.setting.value)
    }, [props.resource.id, props.setting.title])


    if (props.setting.type === "text") {
        return (
            <Box height="2em" width="100%">
                <Box position="absolute" top="0" bottom="0" left="0" width="3em" display="flex" alignItems="center" justifyContent="center">
                    <Notes sx={{fontSize: "1.5em"}}/>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="3em" right="50%" display="flex" alignItems="center" justifyContent="left" sx={{cursor: "default"}}>
                    <Typography fontSize="1em">
                        {props.setting.title}
                    </Typography>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="60%" right="0.5em">
                    <TextField inputProps={{style: {textAlign: "right", fontSize: props.state.size}}} variant="standard" size="small" 
                        value={value}
                        onChange={(event) => {
                            setValue(event.target.value)
                        }}
                    ></TextField>
                </Box>
            </Box>
        )
    } else if (props.setting.type === "select") {
        return (
            <Box height="2em" width="100%">
                <Box position="absolute" top="0" bottom="0" left="0" width="3em" display="flex" alignItems="center" justifyContent="center">
                    <Check sx={{fontSize: "1.5em"}}/>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="3em" right="50%" display="flex" alignItems="center" justifyContent="left" sx={{cursor: "default"}}>
                    <Typography fontSize="1em">
                        {props.setting.title}
                    </Typography>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="60%" right="0.5em" sx={{cursor: "pointer"}}>
                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                        <TextField variant="standard" size="small" 
                            inputProps={{
                                readOnly: true, 
                                style: {
                                    textAlign: "right", 
                                    cursor: "pointer",
                                    fontSize: props.state.size,
                                    
                                }
                            }} 
                            onClick={() => {
                                setTimeout(() => {
                                    setOpen(true)
                                }, 100)
                            }}
                            value={value}
                        >
                        </TextField>
                    
                        <Box position="absolute" top="calc(100%)"width="100%"  display={open ? "block" : "none"} zIndex={999999}>
                            <ClickAwayListener onClickAway={() => {
                                if(open) {
                                    setOpen(false)
                                }
                            }}>
                                <Paper variant="outlined" sx={{zIndex: 1, borderRadius: 0}}>
                                    <List>
                                        {props.setting.options.map((option: string) => (
                                            <ListItem disablePadding key={option}>
                                                <ListItemButton disableGutters sx={{padding: 0}} onClick={() => {
                                                    setValue(option)
                                                    setOpen(false)
                                                }}>
                                                    <ListItemText primary={option} 
                                                        primaryTypographyProps={{
                                                            style: {
                                                                textAlign: "right",
                                                                paddingRight: "0.5em",
                                                                fontSize: props.state.size,
                                                            }
                                                        }}
                                                        sx={{margin: "0.1em"}}
                                                    >

                                                    </ListItemText>
                                                </ListItemButton>
                                            </ListItem>
                                        ))}
                                    </List>
                                </Paper>
                            </ClickAwayListener>
                        </Box>
                    </Box>
                </Box>
            </Box>
        )
    } else if (props.setting.type === "code") {
        return (
            <Box height="2em" width="100%">
                <Box position="absolute" top="0" bottom="0" left="0" width="3em" display="flex" alignItems="center" justifyContent="center">
                    <Code sx={{fontSize: "1.5em"}}/>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="3em" right="50%" display="flex" alignItems="center" justifyContent="left" sx={{cursor: "default"}}>
                    <Typography fontSize="1em">
                        {props.setting.title}
                    </Typography>
                </Box>
                <Box position="absolute" top="0" bottom="0" left="60%" right="0.5em" sx={{cursor: "pointer"}}>
                    <TextField inputProps={{readOnly: true, style: {textAlign: "right", cursor: "pointer", fontSize: props.state.size}}} variant="standard" size="small" value="..."
                        onClick={() => {
                            props.setState({
                                ...props.state,
                                editing: true,
                                resource: props.resource.id,
                                setting: props.setting.title,
                            })
                            // props.state.OpenResourceSetting(props.resource.index, props.setting.title)
                        }}
                    />
                
                </Box>
            </Box>
        )
    } else {
        return (
            <div />
        )
    }
}
export default ResourceSetting