import axios from 'axios'

import { useState, useEffect } from 'react'
import { useTheme } from '@mui/material/styles'

import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import ReactMarkdown from 'react-markdown'
import rehypeRaw from 'rehype-raw'
import rehypeRemark from 'rehype-remark'
import remarkRehype from 'remark-rehype'
import remarkGfm from 'remark-gfm'

import red from '@mui/material/colors/red'

import {marked} from 'marked'

import { createTheme } from '@mui/material/styles';

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});

interface DescriptionProps {

}


export const Description = (props: DescriptionProps) => {

    const [readmeContent, setReadmeContent] = useState('');

  useEffect(() => {
    const fetchReadme = async () => {
      try {
        const response = await fetch(
          'https://api.github.com/repos/michaelckearney/babble/readme'
        );

        if (response.ok) {
          const data = await response.json();
          const markdownContent = atob(data.content); // Decode the base64-encoded content
          setReadmeContent(marked(markdownContent));
        } else {
          // Handle error if the README cannot be fetched
          console.error('Failed to fetch README');
        }
      } catch (error) {
        console.error('Error:', error);
      }
    };

    fetchReadme();
  }, []);


    return (
        <Box position="absolute" top="0" bottom="0" left="2em" right="1em" sx={{overflowY: "scroll", backgroundColor: darkTheme.palette.background.default}} zIndex={2}>
        <Typography color="white" component="div">
            <div 
                dangerouslySetInnerHTML={{ __html: readmeContent }}
                style={{marginBottom: "10%"}}
            />
        </Typography>
        </Box>
    )
}
export default Description