import Editor from '@monaco-editor/react'

import Box from '@mui/material/Box'
import Divider from '@mui/material/Divider'
import Typography from '@mui/material/Typography'
import Paper from '@mui/material/Paper'
import ClickAwayListener from '@mui/material/ClickAwayListener'
import List from '@mui/material/List'
import ListItem from '@mui/material/ListItem'
import ListItemButton from '@mui/material/ListItemButton'


import MoreVert from '@mui/icons-material/MoreVert'
import Close from '@mui/icons-material/Close'
import CloudDoneOutlined from '@mui/icons-material/CloudDoneOutlined'

import Replay from '@mui/icons-material/Replay'
import Stop from '@mui/icons-material/Stop'
import Link from '@mui/icons-material/Link'
import Delete from '@mui/icons-material/Delete'
import Upload from '@mui/icons-material/Upload'
import Download from '@mui/icons-material/Download'

import useTheme from '@mui/material/styles/useTheme'

import green from '@mui/material/colors/green'

import {useRef, useState, useEffect} from 'react'

import {State} from './State'

export const Preview = (props: {
    state: State,
    setState: (state: State) => void,
}) => {

    const handleClick = () => {
        setOpen(false)
        props.setState({
            ...props.state,
            dialog: "download",
        })
    }

    const [output, setOutput] = useState("")

    const [open, setOpen] = useState(false)

    useEffect(() => {
        let result = ""
        for (let i = 0; i < props.state.resources.length; i++) {
            result += props.state.resources[i].name + ":\n"
            result += "    resource: " + props.state.resources[i].resource + "\n"
            result += "    settings:\n"
            for (let j = 0; j < props.state.resources[i].settings.length; j++) {
                if (props.state.resources[i].settings[j].type === "code") {
                    result += "        " + props.state.resources[i].settings[j].title + ": |\n"

                    let lines = props.state.resources[i].settings[j].value.trim().split("\n")
                    let code_result = lines.join("\n            ") + "\n"
                    if (code_result.trim() != "") {
                        result += "            " + code_result
                    }
                } else {
                    result += "        " + props.state.resources[i].settings[j].title + ": '" + props.state.resources[i].settings[j].value + "'\n"
                }
            }
        }
        setOutput(result.trim())
    }, [JSON.stringify(props.state.resources)])

    const theme = useTheme();
    const monacoRef = useRef(null);

    if (theme.palette.background.default === "#fff") {
        theme.palette.background.default = "#ffffff"
    }
    const handleEditorWillMount = (monaco: any) => {
        monaco.editor.defineTheme('theme1', {
            base: props.state.theme === "light" ? "vs" : "vs-dark",
            inherit: true,
            rules: [],
            colors: {
                'editor.background': theme.palette.background.default,
                'scrollbar.shadow': theme.palette.mode === "dark" ? '#000000' : '#ffffff',
            }
        })
    }
    const handleEditorDidMount = (editor: any, monaco: any) => {
        monacoRef.current = monaco;
    }

    return (
        <Paper variant="outlined" sx={{position: "absolute", top: "0em", bottom: "0em", left: "0em", right: "0em", display: props.state.editing ? "none" : "block"}}>
            <Box position="absolute" height="4em" top="0em" left="0em" right="0em">
                <Box position="absolute" width="4em" top="0em" bottom="0em" left="0em" display="flex" alignItems="center" justifyContent="center">
                    <CloudDoneOutlined sx={{fontSize: "2em", color: green[500]}}/>
                </Box>
                <Box position="absolute" top="0em" bottom="0em" left="4em" right="4em">
                    <Box position="absolute" top="0%" bottom="50%" left="0%" right="0%" display="flex" alignItems="end" justifyContent="left" paddingLeft="0.5em">
                        <Typography fontSize="1em" marginBottom="-0.1em">
                            <b>my_service</b>
                        </Typography>
                    </Box>
                    <Box display="flex" position="absolute" top="50%" bottom="0%" left="0%" right="0%" alignItems="start" justifyContent="left" paddingLeft="0.5em">
                        <Typography fontSize="1em" marginTop="-0.1em">
                            active
                        </Typography>
                    </Box>
                </Box>
                <Box position="absolute" top="0em" bottom="0em" right="0em" width="4em" display="flex" alignItems="center" justifyContent="center"
                    sx={{
                        cursor: "pointer",
                    }}
                    onClick={() => {
                        setTimeout(() => {
                            setOpen(true)
                        }, 100)
                        
                    }}
                >
                    <MoreVert sx={{fontSize: "2em"}} />
                </Box>
            </Box>
            <Divider sx={{marginTop: "4em"}} />
            <Box position="absolute" top="4.1em" bottom="0em" left="0em" right="0em">
                <Editor
                    value = {output}
                    beforeMount = {handleEditorWillMount}
                    onMount = {handleEditorDidMount}
                    theme = "theme1"
                    language="ansi"
                    key = {props.state.font + props.state.theme}
                    options = {{
                        readOnly: true,
                        fontSize: props.state.size,
                        minimap: {
                            renderCharacters: false,
                            scale: 2
                        }
                    }}
                />
            </Box>
            <ClickAwayListener onClickAway={() => {
                if(open) {
                    setOpen(false)
                }
            }}>
                <Box position="absolute" top="4em" right="0" height="10em" width="13em" zIndex={999999} display={open ? "block" : "none"}>
                    <Box position="absolute" top="0" bottom="0" left="0" right="-1px">
                    <Paper variant="outlined" sx={{borderRadius: 0}}>
                        <List disablePadding>
                            <ListItem disablePadding sx={{height: "2.5em"}}>
                                <Box position="absolute" top="0" bottom="0" left="1em" right="0em" display="flex" alignItems="start" justifyContent="left">
                                    <Typography paddingTop="0.5em" fontSize="1.1em">
                                        <b>my_service</b>
                                    </Typography>
                                </Box>
                            </ListItem>
                            <ListItem disablePadding key="copy url">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Link sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            copy url
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="edit">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Close sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            exit
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="reactivate">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Replay sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            reactivate
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="deactivate">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Stop sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            deactivate
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="import">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Upload sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            import
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="export">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Download sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            export
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding key="delete">
                                <ListItemButton sx={{height: "2.5em"}} onClick={handleClick}>
                                    <Box position="absolute" top="0" bottom="0" left="0" right="0">
                                        <Box position="absolute" top="0" bottom="0" left="0" width="3.5em" display="flex" alignItems="center" justifyContent="center">
                                            <Delete sx={{fontSize: "1.75em"}} />
                                        </Box>
                                    </Box>
                                    <Box position="absolute" top="0" bottom="0" left="3.75em" right="0" display="flex" alignItems="center" justifyContent="left">
                                        <Typography fontSize="1em">
                                            delete
                                        </Typography>
                                    </Box>
                                </ListItemButton>
                            </ListItem>
                            <ListItem disablePadding sx={{height: "0.5em"}} />
                        </List>
                    </Paper>
                    </Box>
                </Box>
            </ClickAwayListener>
        </Paper>
    )
}
export default Preview