import Inventory2Outlined from '@mui/icons-material/Inventory2Outlined'
import FolderOutlined from '@mui/icons-material/FolderOutlined'
import TableChartOutlined from '@mui/icons-material/TableChartOutlined'
import LibraryBooksOutlined from '@mui/icons-material/LibraryBooksOutlined'
import DescriptionOutlined from '@mui/icons-material/DescriptionOutlined'
import CalendarMonthOutlined from '@mui/icons-material/CalendarMonthOutlined'
import CloudOutlined from '@mui/icons-material/CloudOutlined'

import red from '@mui/material/colors/red'
import blue from '@mui/material/colors/blue'
import green from '@mui/material/colors/green'
import orange from '@mui/material/colors/orange'
import amber from '@mui/material/colors/amber'
import purple from '@mui/material/colors/purple'

import Box from '@mui/material/Box'

export const ResourceIcon = (props: {
    type: string
    size: number
}) => {
    return (
        props.type === 'package' ? <Inventory2Outlined sx={{color: purple[500], fontSize: props.size}}/> :
        props.type === 'folder' ? <FolderOutlined sx={{color: blue[500], fontSize: props.size}} /> :
        props.type === 'table' ? <TableChartOutlined sx={{color: green[500], fontSize: props.size}} /> :
        props.type === 'library' ? <LibraryBooksOutlined sx={{color: amber[500], fontSize: props.size}} /> :
        props.type === 'script' ? <DescriptionOutlined sx={{color: orange[500], fontSize: props.size}} /> :
        props.type === 'routine' ? <CalendarMonthOutlined sx={{color: red[500], fontSize: props.size}} /> :
        props.type === 'endpoint' ? <CloudOutlined /> : <Box />
    )
}
export default ResourceIcon